import Master from '../components/Master';
import Home from '../components/Home';
import '../styles.scss';

export default function Index() {
    return (
        <Master>
            <Home />
        </Master>
    );
}

Index.getInitialProps = async function() {
    const result = await api.query({
                            query: gql`
                                {
                                    users {
                                        id
                                        name
                                        email
                                    }
                                }
                            `});
    return { user: result.data.users[0] };
}